import { useState, memo, useEffect, useCallback } from "react";
import { Button, Input } from "antd";
import "./index.less";

const Son = (props: any) => {
  const { handleClick } = props;
  console.log("son render");
  return (
    <div>
      <p>this is son</p>
      <Button onClick={handleClick}>Click</Button>
    </div>
  );
};

const MemoSon = memo(Son);

const Parent = () => {
  const [clickCount, increaseCount] = useState(0);
  // `useCallback`
  // const handleClick = () => {
  //     console.log('handleClick');
  //     increaseCount(clickCount + 1);
  // }

  // `useCallback``useCallback`
  // useCallbackmemoize
  const handleClick = useCallback(() => {
    console.log("handleClick");
    increaseCount(clickCount + 1);
  }, []);

  return (
    <div className="parent">
      <p>clickCount === {clickCount}</p>
      {/* <Son handleClick={handleClick} /> */}
      <MemoSon handleClick={handleClick} />
    </div>
  );
};

const SonTree = memo((props: any) => {
  console.log(" SonTree render");
  const { onClick } = props;
  const dateBegin = Date.now();
  // 
  while (Date.now() - dateBegin < 600) {}
  useEffect(() => {
    console.log(" SonTree render --- DONE");
  });

  return (
    <div onClick={onClick}>
      <p></p>
    </div>
  );
});

const Box = () => {
  const [text, setText] = useState("Initial value");

  const handleSubmit = useCallback(() => {
    console.log(`Text: ${text}`); // BUG
  }, [text]);

  return (
    <div className="box">
      this is Box
      <Input
        value={text}
        placeholder=""
        onChange={(e) => setText(e.target.value)}
      />
      {/* <Button className='sub-click' onClick={handleSubmit}>this is a useCallback(fn, deps)()</Button>  */}
      <SonTree onClick={handleSubmit} />
    </div>
  );
};

const UseCallBackDemo = () => {
  return (
    <div className="call-back">
      <p>{`const memoizedCallback = useCallback(  () => { doSomething(a, b); }, [a, b], );`}</p>
      <p>
         useCallback
        memoized 
      </p>
      <p></p>
      <p></p>
      <p>
        <strong>useCallback</strong>
      </p>
      <Parent />
      <div />
      <Box />
    </div>
  );
};

export default UseCallBackDemo;
