import { Link, useNavigate } from "react-router-dom";
import { LeftCircleOutlined } from "@ant-design/icons";
import { box, demo, qiang } from '../../../assets/pic/three/index';
import './index.less';

const ThreeList = () => {

  const listdemo = [
    { id: 1, name: '', url: '/three/demo', gif: box },
    { id: 2, name: '', url: '/three/geometry', gif: demo },
    { id: 3, name: '', url: '/three/model', gif: qiang },
    { id: 4, name: '', url: '/three/externalmodels', gif: box },
    // { id: 4, name: '', url: '/three/line', gif: demo },
  ];

  const handleJump = () => {
    window.open('https://threejs.org/manual/#zh/primitives');
  }
  let navigate = useNavigate();
  const handleBackIndex = () => {
    navigate('/');
  }

  return (
    <div className="three">
      <div className='three-title'>
        <div className='three-title-back' onClick={handleBackIndex}>
          <LeftCircleOutlined />
          <span></span>
        </div>
        <div>3D demo</div>
        <div className='three-title-info'>
          <div>3D</div>
          <div></div>
          <div>   cool girl</div>
          <div>
            <span onClick={handleJump} style={{ color: '#0d8fea', fontWeight: 'bold', cursor: 'pointer' }}></span>
            
          </div>
        </div>
      </div>
      <div className="three-list">
        <div className="three-list-content">
          {
            listdemo.map((item, index) => {
              return (
                <Link key={index} to={item.url} className='three-list-content-item'>
                  <div>
                    <img src={item.gif} alt="box" />
                  </div>
                  <div className='three-list-content-item-title'>{item.name}</div>
                </Link>
              )
            })
          }
        </div>
      </div>
    </div>
  )
}

export default ThreeList;