import { useState, memo } from "react"

import './index.less';

const Son = (props: any) => {
  const { numA, noUseClick, text } = props;
  console.log(Math.random());
  console.log(text);
  return (
    <div className="son" >
      <div>this is son Node</div>
      {/* <div >props numA = {numA}</div> */}
      <div onClick={noUseClick} >props numA = {numA}</div>
    </div>
  ) 
};

const WarpperSon = memo(Son);

const Parent = () => {
  const [numA, setNumA] = useState(0);
  const [numB, setNumB] = useState(10);


  const handleAdd = () => {
    setNumB(numB + 1);
  }

  const noUseClick = () => {
    // console.log(Math.random())
  };

  return (
    <div className='parent'>
      <div>this is Parent Node: </div>
      <div>this is numA: {numA}</div>
      <div className="parent-num-b" onClick={handleAdd}>this is numB: {numB} (,numB) </div>
      {/* <Son noUseClick={noUseClick} numB={numB} numA={numA} text={' noUseClick memo numB '} /> */}
      <WarpperSon numB={numB} numA={numA} text={' numB , SonnumBnumB'} />
      <WarpperSon numA={numA} text={' numB '} />
      <WarpperSon noUseClick={noUseClick} numA={numA} text={'WarpperSon noUseClick numA '} />
      {/* <WarpperSon numA={numA} text={'WarpperSon numA '} /> */}
    </div>
  )
}

const MemoDemo = () => {
  return (
    <div className="memo">
      <div>React.memo</div>
      <div> props  React.memo </div>
      <div> props </div>
      <div> React.memo  useStateuseReducer  useContext  Hook state  context </div>
      <Parent/>
    </div>
  )
}

export default MemoDemo;